.@{container-class} .fn-m-job-operation-bar {
	position: relative;
	margin-bottom:15*@rex;

	@zindex-dropdown: 890;

	.tab{
		text-align: center;
		background: #fff;
		position: relative;
		z-index: @zindex-dropdown;

		.item{
			position: relative;
			padding: 10*@rex 0;
			text-align: center;

			&:before{
				position: absolute;
				top: 10*@rex;
				right: 1*@rex;
				content: '';
				width: 1*@rex;
				height: 20*@rex;
				background: #d9d9d9;
			}

			&:last-child:before{
				background: 0 0;
			}

			i{
				margin-left:2%;
			}
		}
	}

	.dropdown{
		.mask{
			.fixed(0,_,_,0);
			.size(100%);
			z-index: -1;
		}

		.panel{
			.absolute(100%,_,_,0);
			z-index: -1;
			width: 100%;
			background: #fff;
			transform: translateY(-100%);
			transition: transform 0.3s ease;

			.item{
				display: block;
				overflow: hidden;
				padding: 10*@rex;
				border-bottom: 1*@rex solid @border-color;

				&:first-child{
					border-top: 1*@rex solid @border-color;
				}

				&.active{
					.iconfont{
						color: @main-color;
						    visibility: visible;
					}
				}

				.iconfont{
					float: right;
					margin-right: 10*@rex;
					    visibility: hidden;
				}
			}
		}

		&.active{
			.mask{
				z-index: @zindex-dropdown - 1;
			}

			.panel{
				z-index: @zindex-dropdown - 1;
				transform: translateY(0%);
			}
		}
	}

