.@{container-class}{

	.button-area{
		padding:14*@rex 24*@rex 24*@rex 24*@rex;
		text-align:center;
		display:flex;
		flex-flow: column;
		.button{
			background:@main-color;
			flex:1;
			color:#fff;
			padding:10*@rex 0;
			border-radius:5*@rex;

			&.odd{
				margin-top:20*@rex;
				background:#fff;
				color:@main-color;
			}
		}

		.button.b{
			width:100%;
		}
	}

}